import Bus from '../eventbus'
export default {
  name: 'BBB',
  data () {
    return {
      msg: 'Welcome to Bbb',
      ccdata: '',   // 来自cc的数据（eventbus）
    }
  },
  created () {
    // 监听 cc 传递的数据(eventbus)
    Bus.$on('fromcc', this.getcc);
  },
  methods: {
    getcc (val) {
      console.log('val: ', val);
      this.ccdata = val;
    }
  },
  beforeDestroy () {
    // 注销事件（防止多次触发，新旧组件都要）
    Bus.$off('fromcc', this.getcc);
  }
}




